export class Chem {
    static molecule(molecule, superscript = null) {
        return molecule.replaceAll("*", "•").replaceAll("#", "\\text{ }").replaceAll(/([A-Z][a-z]*)/g, "\\text{$1}").replaceAll(/(?<!(?: |^|•|\d|\/))((\d|\/)+)/g, "_{$1}").replaceAll(/\b([a-z]|\d|^\d) /g, "$1\\text{ }").replaceAll(/(\d+)\/(\d+)/g, "\\frac{$1}{$2}") + (superscript != null ? `^{${superscript}}` : "");
    }
    static equation(equation) {
        return Chem.molecule(equation.replaceAll("->", "#⭢#"));
    }
}
